"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Api from "@/app/services/logic";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import {
  Item,
  ItemActions,
  ItemContent,
  ItemDescription,
  ItemTitle,
} from "@/components/ui/item";

interface Props {
  params: { id: string };
}

export default function EditTransactionPage({ params }: Props) {
  const { id } = params;
  const router = useRouter();
  const api = new Api();

  const [categories, setCategories] = useState([]);
  const [transaction, setTransaction] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        const cats = await api.getAllCategories();
        setCategories(cats);

        const transactions = await api.getTransactions({ page: 1, limit: 1000 });
        const tx = transactions.find((t: any) => String(t.id) === id);
        if (!tx) {
          setError("Transaction not found");
          setTransaction(null);
        } else {
          // Format date for input type date
          tx.date = new Date(tx.date).toISOString().split("T")[0];
          setTransaction(tx);
        }
      } catch {
        setError("Failed to load data");
        setTransaction(null);
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, [id]);

  function handleChange(e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) {
    if (!transaction) return;
    const { name, value } = e.target;
    setTransaction({ ...transaction, [name]: name === "amount" ? parseFloat(value) : value });
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!transaction || !transaction.id) return;

    setSaving(true);
    setError(null);
    try {
      await api.updateTransaction(transaction.id, {
        amount: transaction.amount,
        category: transaction.category,
        description: transaction.description,
        type: transaction.type,
        date: new Date(transaction.date).toISOString(),
      });
      router.push(`/transactions/${transaction.id}`);
    } catch {
      setError("Failed to save changes");
    } finally {
      setSaving(false);
    }
  }

  if (loading) return <div>Loading...</div>;
  if (error) return <div className="text-red-600 p-4">{error}</div>;
  if (!transaction) return null;

  return (
    <div className="max-w-xl p-6 rounded-md bg-[#f5f6fb] dark:bg-[#0c1017] dark:text-white shadow-md mx-auto mt-8">
      <Item variant="muted">
        <ItemContent>
          <ItemTitle className="mb-3">Edit Transaction ID: {transaction.id}</ItemTitle>
          <form onSubmit={handleSubmit} className="flex flex-col gap-5">
            <label className="block">
              <span className="font-medium">Type</span>
              <select name="type" value={transaction.type} onChange={handleChange} className="w-full rounded border p-2 mt-1 bg-white dark:bg-gray-700 dark:text-white">
                <option value="expense">Expense</option>
                <option value="income">Income</option>
              </select>
            </label>

            <label className="block">
              <span className="font-medium">Amount</span>
              <input
                type="number"
                name="amount"
                step="0.01"
                min="0"
                value={transaction.amount}
                onChange={handleChange}
                className="w-full rounded border p-2 mt-1 bg-white dark:bg-gray-700 dark:text-white"
                required
              />
            </label>

            <label className="block">
              <span className="font-medium">Category</span>
              <select name="category" value={transaction.category} onChange={handleChange} className="w-full rounded border p-2 mt-1 bg-white dark:bg-gray-700 dark:text-white" required>
                {categories.map((cat: any) => (
                  <option key={cat.id} value={String(cat.id)}>
                    {cat.name}
                  </option>
                ))}
              </select>
            </label>

            <label className="block">
              <span className="font-medium">Description</span>
              <textarea
                name="description"
                value={transaction.description}
                onChange={handleChange}
                rows={3}
                className="w-full rounded border p-2 mt-1 bg-white dark:bg-gray-700 dark:text-white resize-none"
                required
              />
            </label>

            <label className="block">
              <span className="font-medium">Date</span>
              <input
                type="date"
                name="date"
                value={transaction.date}
                onChange={handleChange}
                className="w-full rounded border p-2 mt-1 bg-white dark:bg-gray-700 dark:text-white"
                required
              />
            </label>

            {error && <div className="text-red-600">{error}</div>}

            <Button type="submit" disabled={saving} className="mt-4">
              {saving ? "Saving..." : "Save Changes"}
            </Button>
          </form>
          <Separator className="my-4" />
          <ItemActions>
            <Button asChild>
              <a href={`/transactions/${transaction.id}`} className="text-blue-600 hover:underline">
                Cancel
              </a>
            </Button>
          </ItemActions>
        </ItemContent>
      </Item>
    </div>
  );
}
